@import url("./app/layout/dashboard.css");
#root {
    height: 100vh;
  width: 100vw;
  overflow: hidden;
  display: grid;
  grid-template-columns: 1fr 5fr;
  grid-template-rows: auto 1fr;
}

.header {
    grid-column: 2/7;
    grid-row: 1 / 2;
    text-align: center;
    
}
.sidebar {
    grid-column: 1 / 2;
    grid-row: 1 / 3;
}
main {
    grid-column: 2/7;
    grid-row: 2 / 2;
    overflow-y: auto;
}


