/* Grid pagina dashboard */
.dashboard {
    padding: 3%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(5, 1fr);
    place-items: center;
    gap: 5%;
}
.dashboard > section {
    box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
    border-radius: 20px;
    padding: 5%;
    width: 100%;
}
progress[value] {
    -webkit-appearance: none;
     appearance: none;
  }
:progress{
    border-radius: 2rem;
}
/* FIRST ROW */
.new-booking {
    grid-column: 1 / 1;
    grid-row: 1 / 2;
}
.schedule {
    grid-column: 2 / 2;
    grid-row: 1 / 2;
}
.check-in {
    grid-column: 3 / 3;
    grid-row: 1 / 2;
}
.check-out {
    grid-column: 4 / 4;
    grid-row: 1 / 2;
}


/* SECOND ROW */
.booking-schedule {
    grid-column: 1 / 3;
    grid-row: 2 / 5;
}
.reservation-status{
    grid-column: 3 / 5;
    grid-row: 2 / 3;
}
.available-room-today {
    grid-column: 3 / 4;
    grid-row: 3 / 4;
}
.sold-room-today {
    grid-column: 4 / 5;
    grid-row: 3 / 4;
}
.data-report {
    grid-column: 3 / 5;
    grid-row: 4 / 5;
}


/* THIRD ROW */
.latest-reviews {
    grid-column: 1 / 5;
    grid-row: 5;
}